*, :before, :after {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.watch__circle {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: relative;
}

.watch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__hand {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.watch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  transform: rotate(0);
}

.watch__hand--second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  transform: rotate(0);
}

.watch--speed-up .watch__hand--minute {
  animation-duration: 600s;
}

.watch--speed-up .watch__hand--second {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}
